.ficha{
    color: white;
    border-radius: 10px;
    display: flex;
    justify-content: space-between
}
.backIcon:hover{
    cursor: pointer;
    transition: .4s;
    transform: scale(1.3)
}
.backIcon{
    color:white
}
.color-white{
    border-radius: 10px;
    color: white !important;
    border: 1px solid white
}
.color-black{
    border-radius: 10px;
    color: rgb(29, 30, 112) !important;
    border: 1px solid white;
    background: gray
}
.itemDeuda{
    cursor: pointer
}
.danger-zone{
    display: flex;
    justify-content: space-evenly
}
.nav{
    color:white
}
.name{
    color: white;
    text-shadow: 3px 3px 2px black;
    user-select: none
}
.nombreItem{
    user-select: none;
    cursor: pointer;
    animation: edit 1s infinite alternate-reverse;
}
.newInfo{
    animation: newInfo 1s both;
}
.dataItem{
    user-select: none;
    cursor: pointer;
    animation: delete 1s infinite alternate-reverse;
}
@keyframes delete{
    from{
        background: transparent
    }
    to{
        background: red
    }
}
@keyframes newInfo{
    from{
        transform: scale(0)
    }
    to{
        transform: scale(1)
    }
}
@keyframes edit{
    from{
        background: transparent
    }
    to{
        background: green
    }
}